#manager-page{
  background-image: -webkit-gradient(linear, left bottom, left top, from(#e2eaf6), color-stop(#bed6e3), color-stop(#99c4ca), color-stop(#7db0a8), to(#6f9b80));
  background-image: -webkit-linear-gradient(bottom, #e2eaf6, #bed6e3, #99c4ca, #7db0a8, #6f9b80);
  background-image: -o-linear-gradient(bottom, #e2eaf6, #bed6e3, #99c4ca, #7db0a8, #6f9b80);
  background-image: linear-gradient(to top, #e2eaf6, #bed6e3, #99c4ca, #7db0a8, #6f9b80);
  padding-top: 3.5rem;
  height: 100vh;
  overflow: hidden;
}

.manager-body{
  background-color: white;
  padding-top: 12rem;
  padding-bottom: 14rem;
}

@media (max-width: 768px) {
  .manager-body{
    padding-top: 8rem;
    padding-bottom: 14rem;
  }
}



